.wp-block-group {
	&.has-background {
		padding: 5px;
	}
}

.wp-block-columns {
	margin-bottom: 0;
	
	&.has-background {
		padding: 5px;
	}

	&.is-selected,
	&.has-child-selected {
		&::before {
			content: '';
			position: absolute;
		}
	}

	&::before {
		pointer-events: none;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		color: rgba(0,0,0,0.1);
		z-index: 0;
		background-image: repeating-linear-gradient(to right, currentColor, currentColor 1px, transparent 1px, transparent);
		background-size: 8.33%;
		border-top: 1px solid #efefef;
    	border-bottom: 1px solid #efefef;
	}
}

.wp-block-column {
	& ~ .wp-block-column {
		margin-left: 0!important;
	}
}